// pages/posts/[slug].tsx
import fs from "fs";
import path from "path";
import matter from "gray-matter";
import { GetStaticPaths, GetStaticProps } from "next";
import { Post } from "../../lib/posts";
import Header from "../../components/Header";
import AvatarSection from "../../components/AvatarSection";
import ReactMarkdown from "react-markdown";

interface PostPageProps {
  post: Post;
}

export default function PostPage({ post }: PostPageProps) {
  return (
    <div
      style={{
        fontFamily: "'Microsoft YaHei', Arial, sans-serif",
        padding: "2rem",
      }}
    >
      <Header />
      <AvatarSection />

      <article style={{ maxWidth: "800px", margin: "2rem auto" }}>
        {/* Â∞ÅÈù¢ */}
        {post.cover && (
          <img
            src={post.cover}
            alt={post.title}
            style={{
              width: "100%",
              borderRadius: "8px",
              marginBottom: "1rem",
              objectFit: "cover",
              height: "300px",
            }}
          />
        )}

        {/* Ê†áÈ¢òÂíåÊó•Êúü */}
        <h1 style={{ fontSize: "2rem", marginBottom: "0.5rem" }}>
          {post.title}
        </h1>
        <p style={{ color: "#666", marginBottom: "2rem" }}>{post.date}</p>

        {/* Markdown Ê≠£Êñá */}
        <ReactMarkdown
          children={post.content}
          components={{
            h1: ({ node, ...props }) => (
              <h1
                style={{ fontSize: "1.8rem", margin: "1.5rem 0" }}
                {...props}
              />
            ),
            h2: ({ node, ...props }) => (
              <h2
                style={{ fontSize: "1.5rem", margin: "1.2rem 0" }}
                {...props}
              />
            ),
            p: ({ node, ...props }) => (
              <p style={{ lineHeight: "1.8", margin: "0.8rem 0" }} {...props} />
            ),
            img: ({ node, ...props }) => (
              <img
                style={{
                  maxWidth: "100%",
                  borderRadius: "6px",
                  margin: "1rem 0",
                }}
                {...props}
              />
            ),
          }}
        />
      </article>
    </div>
  );
}

// Ëé∑ÂèñÊâÄÊúâÊñáÁ´†ÁöÑ slug
export const getStaticPaths: GetStaticPaths = async () => {
  const postsDirectory = path.join(process.cwd(), "posts");
  const filenames = fs.readdirSync(postsDirectory);

  const paths = filenames.map((filename) => ({
    params: { slug: filename.replace(/\.md$/, "") },
  }));

  return { paths, fallback: false };
};

// Ê†πÊçÆ slug ÁîüÊàêÊñáÁ´†ÂÜÖÂÆπ
export const getStaticProps: GetStaticProps = async ({ params }) => {
  const slug = params?.slug as string;
  const postsDirectory = path.join(process.cwd(), "posts");
  const filePath = path.join(postsDirectory, slug + ".md");
  const fileContents = fs.readFileSync(filePath, "utf8");
  const { data, content } = matter(fileContents);

  const post: Post = {
    slug,
    title: data.title,
    date: data.date,
    summary: data.summary,
    tags: data.tags || [],
    cover: data.cover || "",
    content,
  };

  return { props: { post } };
};
